import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-expense-invoice-history',
  templateUrl: './expense-invoice-history.component.html',
  styleUrls: ['./expense-invoice-history.component.scss']
})
export class ExpenseInvoiceHistoryComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "vendorInvoiceNo";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  accountUserDetails:any;

  vendorDataList:any;
  isVendorDataLoaded:boolean = false;
  vendorCategoryData:any;

  selectedInput:string = "";
  columnField: any = {}

  columnData:any;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  public params = {
		apartmentId: this.sessionService.apartmentId
	  }
	  
  constructor(
    private route: ActivatedRoute,
    private vendorService:VendorService,
  	private accountsService:AccountsService,
    private lookupService: LookupService,
    private sessionService:SessionService,
    private router:Router
  ) { }

  

  getCategoryName(id){
    
    var data = _.filter( this.vendorCategoryData, function( item ) {
      if(item.lookupValueId === parseInt(id))
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  

  
  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  

  onGlSearchFilter() {

    if(this.invoiceData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.invoiceData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onEditInvoiceHist', ['$event.detail'])
  onEditInvoiceHist(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let voucherNo = dataRecord.voucherNo
    this.router.navigateByUrl('/ams/expense/post-invoice/' + voucherNo);
  }
  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Vendor Invoice ID',
         datafield: 'vendorInvoiceNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'Posted By',
         datafield: 'postedBy',
         cellsrenderer: cellsrenderer,         
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Voucher No',
         datafield: 'voucherNo',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Due',
         datafield: 'dueAmount',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Amount Paid',
         datafield: 'expenseHead',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     },{
         text: 'Actions',
         cellsalign: 'center', 
         align: 'center',
         width: 120,
         cellsrenderer: (row: number): string => {
           return '<div class="simple-actions">'
            + '<a href="javascript:void(0)" class="mr-2" onClick="editTicketEvent('+ row +')">'
              + '<svg class="edit" xmlns="http://www.w3.org/2000/svg" " viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-edit"><path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path><path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path></svg>'
            +'</a>'
           +'</div>'
         },
         renderer: columnrenderer
     }];

   this.vendorService.getVendorByApartmentId(this.params).subscribe((res:any) => {

      this.vendorDataList = res.filter(item => {
        return item.vendorId == this.route.params['value'].id;
      });

      this.isVendorDataLoaded = true;

      let categoryParams = {
        LookupTypeId: 57,
        ApartmentId: this.sessionService.apartmentId
      }

      this.lookupService.getLookupValueByLookupTypeId(categoryParams).subscribe((res:any) => {

        this.vendorCategoryData = res.filter(item => {
            return item.isActive;
        });

      });


  });

  	var accountParams = {
      apartmentId: this.sessionService.apartmentId,
      groupId: 3
    }

    this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {
      this.glAccountListData = res;
    });

    let params = {
      VendorID:this.route.params['value'].id
    }
  	this.accountsService.getVendorPaymenntHistoryById(params).subscribe((res:any) => {


  		var invoiceDataList = res;

  		this.totalItems = invoiceDataList.length;
      
      
      this.gridSourceData = {
        localdata: invoiceDataList,
        datatype: "array"
      }

      this.invoiceDataList = new jqx.dataAdapter(this.gridSourceData);

  		if(this.totalItems>this.itemLimit){
			  this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
      }

      this.isInvoiceDataLoaded = true;
      
  	})


  }

}
function editTicketEvent(row){
  var event = new CustomEvent('onEditInvoiceHist', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editTicketEvent = editTicketEvent;