import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ExpenseInvoiceHistoryComponent } from './expense-invoice-history.component';

export const routes: Routes = [
  { path: '', redirectTo: 'view', pathMatch : 'full' },
  { path: 'view', component: ExpenseInvoiceHistoryComponent },
  { path: 'view/:id', component: ExpenseInvoiceHistoryComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpenseInvoiceHistoryRoutingModule { }
